import React, { Component } from "react";
import style from "./Login.css";

class  Login extends Component{
state={
    username:'',
    password:'',
    error:''
}

render(){
     const loginHandler=()=>{
       const username =  this.state.username;
       const password = this.state.password;
if(username){
    if((username.length < 11 && username.length > 2) && (password.length < 11 && password.length > 2) )
    {console.log(username);
    this.props.history.push('./burger-builder');
    }
    else
    this.setState({error:'Username and Password Should be between 3 and 7 characters'});        
    
}
else{
    this.setState({error:"Please Enter Username & Password"})
}

 
    }
  
    return (
      <div className={style.login}>
        <header className={style.title}>Login</header>
        <span className={style.error}>{this.state.error}</span>
        <form>
            <input type="text" onChange={e=>this.setState({username:e.target.value})} placeholder="Username"/><br/>
            <input type="text" onChange={e=>this.setState({password:e.target.value})} placeholder="Password"/><br />
        <button type="button" onClick={loginHandler}>Login</button>
            
        </form>

      </div>
    );
  };
  
 
  
}
export default Login;


